<template>
  <div :class="themeColor[theme] && themeColor[theme].back" class="p-5 text-black text-base md:text-xl xl:text-2xl flex justify-between darkMode">
    <h3 class="font-extrabold darkMode">국내.외 택배조회 시스템</h3>
    <div class="darkMode">
      <span class="mx-5">테마 : {{ theme }}</span>
      <button class="mx-1 md:mx-2 xl:mx-3" @click="theme='orange'">orange</button>
      <button class="mx-1 md:mx-2 xl:mx-3" @click="theme='rose'">rose</button>
      <button class="mx-1 md:mx-2 xl:mx-3" @click="theme='cyan'">cyan</button>
      <button class="mx-1 md:mx-2 xl:mx-3" @click="theme='fuchsia'">fuchsia</button>
    </div>
    <div class="basis-1/12 darkMode">
      <ul class="flex justify-between">
        <li class="basis-2/4 text-center cursor-pointer">
          <font-awesome-icon :icon="isDark === true ? 'sun' : 'moon'" class="text-2xl" @click="isDark === true ? isDark = false : isDark = true" />
        </li>
        <li class="basis-2/4 text-center cursor-pointer">
          <font-awesome-icon icon="globe" class="text-2xl" />
        </li>
      </ul>
    </div>
  </div>
  <div class="w-4/5 md:w-3/5 mx-auto darkMode">
    <img :src="isDark === false ? require(`@/assets/images/main_logo.png`) : require(`@/assets/images/main_logo_dark.png`)" alt="parcel logo" class="mx-auto w-1/4">
  </div>
  <div class="w-4/5 md:w-3/5 xl:w-4/12 mx-auto my-10 flex bg-[#f7f8f9] rounded items-center pt-2 flex-wrap darkMode">
    <div class="border-b border-slate-400 basis-full px-5 py-2 flex justify-center items-center text-sm darkMode">
      <span class="basis-[30%] text-center mr-5 darkMode">국내 / 국외 선택</span>
      <button @click="isBtn = 1; t_code='04';" :class="[
        isBtn==1 && themeColor[theme] && themeColor[theme].active,
        // isBtn이 1번이고(위의 클릭 이벤트에 의해 버튼을 눌렀을때 isBtn 값이 1로 바뀐다.) themColor의 theme가 있다면 active(활성화)됐을때 색깔을 바꿔주세요
        themeColor[theme] && themeColor[theme].hover,
        //  hover 됐을때 hover색깔을 주세요
          isBtn == 1 ? ' text-white' : 'text-black']
        // 버튼을 눌렀을때 글자색깔을 white로 바꿔주고 아닐때는 검은색으로 나타내어 주세요
        " class="text-sm border p-1 px-5 rounded hover:text-white mr-4 darkMode">국내</button>
      <button @click="isBtn = 2; t_code='12';" :class="[
        isBtn == 2 && themeColor[theme] && themeColor[theme].active,
        // isBtn이 2번이고 themColor의 theme가 있다면 active(활성화)됐을때 색깔을 바꿔주세요
        themeColor[theme] && themeColor[theme].hover,
        //  hover 됐을때 hover색깔을 주세요
          isBtn == 2 ? ' text-white' : 'text-black']
        // 버튼을 눌렀을때 글자색깔을 white로 바꿔주고 아닐때는 검은색으로 나타내어 주세요
          " class="text-sm border p-1 px-5 rounded hover:text-white darkMode">국외</button>
    </div>
    <select v-model="t_code" class="mt-1 border darkMode">
      <option v-for="e in Company" :key="e" :value="e.Code">{{ e.Name }}</option>
      <!-- 택배사 이름이 나오고 택배사 이름을 클릭하면 택배사 코드값이 저장된다 -->
    </select>
    <div class="basis-full py-4 text-center">
      <input type="text" placeholder="운송장 번호 입력" class="border w-[80%] px-5 py-2 rounded-md outline-sky-300" id="t_invoice" v-model="t_invoice" @input="bindNumber">
      <!-- 숫자만 적을 수 있도록 되게 할려면 bindnumber를 지정할려면 methods에서 작성하고 v-model값을 지정할려면 watch에 작성한다. -->
    </div>
    <div class="w-full text-center darkMode">
      <button @click="PostList(), isBtn2 = 1" :class="[
        isBtn2 == 1 && themeColor[theme] && themeColor[theme].active,
        themeColor[theme] && themeColor[theme].hover,
        isBtn2 == 1 ? 'text-white' : 'text-black']
        " class="w-full py-3 rounded-md dark:bg-[#d9d9d9] dark:text-[#292929]">조회하기</button>
    </div>
  </div>
  <p class="text-center text-2xl text-red-500 font-bold darkMode">{{ errorMsg }}</p>

  <div class="w-full darkMode" v-if="isShow && errorMsg === ''">
    <!-- isShow가 true이고 errorMsg 가 없을때 밑에 내용을 보여주세요 -->
    <div class="flex justify-center py-10 px-5 flex-wrap items-center text-center darkMode">
      <span class="text-xl basis-[45%] font-bold mr-5 mb-5">운송장 번호</span>
      <h3 class="text-2xl basis-[45%] font-bold mb-5">{{ Trackings.invoiceNo }}</h3>
      <span class="text-xl basis-[45%] font-bold mr-5">택배사</span>
      <h3 class="text-xl basis-[45%] font-bold">{{ TrackingCode[0] }}</h3>
      <!-- filter를 하게되면 배열로 반환된다. 즉, TrackingCode는 배열로 이루어진 변수이다 따라서 위에 template에서 {{ TrackingCode }}를 넣어줄때 배열이라서 값이 제대로 나타날려면 {{ TrackingCode[0] }}이렇게 배열 안의 첫번째 값이 나타나도록 한다. -->
    </div>

    <div class="my-5 flex justify-around darkMode">
      <div class="relative" v-for="level in PostListName.length" :key="level">
        <!-- <img v-if="Trackings.level === level" :src="require(`@/assets/images/ic_tropical_delivery_step${level}_on.png`)" />
        <img v-else :src="require(`@/assets/images/ic_tropical_delivery_step${level}_off.png`)" /> -->
  
        <!-- 위는 if문이고 아래는 if문인데 3항연산자로 나타낸것이다 둘 다 아무거나 써도 상관 없지만 코드는 짧은 것이 좋아서 밑에 것을 쓰는 것이 좋다. -->
  
        <img :src="Trackings.level - 1 === level
        ?
         require(`@/assets/images/ic_tropical_delivery_step${level}_on.png`) // 참일때 나타내는 값, 밑에는 거짓일때 나타내는 값
        : 
        require(`@/assets/images/ic_tropical_delivery_step${level}_off.png`)">
        <p>{{ PostListName[level-1] }}</p>
      </div>
    </div>
  <!-- require은 src에서 바인딩 : 했을때 이미지를 당겨올때 쓴다. 즉 require은 :src 처럼 src를 바인딩 했을때만 쓸 수 있고 require은 이미지 파일 경로를 안내해 주는것이다. -->
  
    <!-- <div class="py-5 flex flex-col flex-wrap justify-evenly basis-auto content-center h-[600px]">
      <div class="inf pl-20 py-5 mx-32 h-30 relative" v-for="(e, index) in Trackings.trackingDetails.slice().reverse()" :key="index"> -->
        <!-- .slice().reverse()를 붙이면 배열의 순서를 거꾸로 나타낸다. 즉, 여기서는 Trackings.trackingDetails의 배열안의 순서를 거꾸로 나타낸 것이다. -->
        <!-- <p>{{ index }}</p>
        <p>{{ e.where }} | {{ e.kind }}</p>
        <p>{{ e.telno }}</p>
        <p>{{ e.timeString }}</p>
      </div>
    </div> -->

    <table class="py-5 mx-auto my-24 darkMode">
      <th class="border-b-2">배송시간</th>
      <th class="border-b-2">현재위치</th>
      <th class="border-b-2">배송상태</th>
      <tr class="border-b-2" v-for="(e, index) in Trackings.trackingDetails.slice().reverse()" :key="index">
        <td class="w-96 text-center py-4">{{ e.timeString }}</td>
        <td class="w-96 text-center py-4">{{ e.where }}</td>
        <td class="w-96 text-center py-4">{{ e.kind }}</td>
      </tr>
    </table>
  </div>

  <!-- 운송장 번호 : 363817427036  -->
</template>

<script>

import axios from "axios"
import {useDark, useToggle} from '@vueuse/core'
// import Tracking from './assets/Tracking.json'
// import CarrierList from './assets/Company.json'
// 임포트 가져오다
// 뒤에 붙은 이름은 작명 아무거나 작성 가능하나, 한글은 자제
// 어디에서 가져오냐..
// 소스위치를 설정
export default {
  name: 'App',
  data() {
    return {
      isBtn: 0,
      isBtn2: 0,
      t_key: "5eWNhbdIEqkdnrV2XsJHXQ", 
      t_code: "04",
      t_invoice: "",
      Trackings: [],
      Carriers : [],
      isShow: false,
      isDark: useDark(),
      toggleDark: useToggle(useDark()),
      errorMsg: '',
      theme: 'orange',
      themeColor: {
        "orange" :{
          "back" : "bg-orange-500", "hover": "bg-orange-300",
          "active": "bg-orange-400", "text": "text-orange-500"
        },
        "rose" :{
          "back" : "bg-rose-500", "hover": "bg-rose-300",
          "active": "bg-rose-400", "text": "text-rose-500"
        },
        "cyan" :{
          "back" : "bg-cyan-500", "hover": "bg-cyan-300",
          "active": "bg-cyan-400", "text": "text-cyan-500"
        },
        "fuchsia" :{
          "back" : "bg-fuchsia-500", "hover": "bg-fuchsia-300",
          "active": "bg-fuchsia-400", "text": "text-fuchsia-500"
        },
      },
      PostListName: ["상품인수", "상품이동중", "배송지도착", "배송출발", "배송완료"],

      // 택배 운송장 조회할때 위의 기본적으로 t_key, t_code, t_invoice 변수가 필요함.
    }
  },

  created(){
    axios.get("https://info.sweettracker.co.kr/api/v1/companylist?t_key=5eWNhbdIEqkdnrV2XsJHXQ")
    .then((res)=>{
      this.Carriers = res.data.Company
      console.log(this.Carriers)
    })
    .catch((error)=>{
      console.log(error)
    })
  },
  methods:{
    bindNumber(){
      return this.t_invoice = this.t_invoice.replace(/[^0-9]/g, '')
      // 운송장 번호가 숫자만 입력할 수 있도록 지정
    },
    PostList(){
      if(this.t_invoice === ''){ // 운송장 번호 입력란이 비어있을 경우
        this.errorMsg = "운송장 번호 입력하세요!!!!"; //에러메세지를 띄운다
        document.querySelector("#t_invoice").focus()
        // 운송장 번호 입력란에 커서가 깜빡거리게 해준다.
        // .focus() - 커서를 깜빡거리게 해준다. 단 한 칸에만 사용가능
        // 예) 아이디와 비밀번호 입력칸이 있다면 둘다 focus를 사용할 수 없다는 뜻...
        return
      }
      axios.get("http://info.sweettracker.co.kr/api/v1/trackingInfo",{
        params:{
          t_code: this.t_code,
          t_invoice: this.t_invoice,
          t_key: this.t_key
        }
      }).then((res)=>{
        console.log(res)
        if(res.data.code === '104'){
          this.errorMsg = res.data.msg
        }else{
          this.Trackings = res.data // 코드가 에러가 아니라면 밑에 운송장을 띄워라
          this.errorMsg = ''
          this.isShow = true
        }


      }).catch((error)=>{
        console.log(error)
      })
    }
  },
  watch:{
    t_invoice(){
      return this.t_invoice = this.t_invoice.replace(/[^0-9]/g, '')
            // 운송장 번호가 숫자만 입력할 수 있도록 지정

    }
  },
  computed:{
    TrackingCode(){
      return this.Carriers.filter((e)=>{
        return e.Code === this.t_code
      }).map(e => e.Name);
    }, // filter를 하게되면 배열로 반환된다. 즉, TrackingCode는 배열로 이루어진 변수이다 따라서 위에 template에서 {{ TrackingCode }}를 넣어줄때 배열이라서 값이 제대로 나타날려면 {{ TrackingCode[0] }}이렇게 배열 안의 첫번째 값이 나타나도록 한다.
    Company(){
      return this.Carriers.filter((data)=>{
        if(this.isBtn == 1){
          return data.International == 'false'
        }else{
          return data.International == 'true'
        }
      })
    }
  },
  // /api/v1/companylist
  components: {
    
  },
  mounted() {
    console.log(this.Trackings)
  }
}
</script>

